body {
  margin: 0;
  font-family: "Manrope", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.heading1 {
  font-size: 33px;
  font-weight: bold;
}
.heading2 {
  font-size: 24px;
  font-weight: bold;
}
.heading3 {
  font-size: 20px;
  /* font-weight: bo; */
}

.body1-bold {
  font-size: 16px;
  font-weight: bold;
}
.body1-bold-underlined {
  font-size: 16px;
  font-weight: bold;
  text-decoration: underline;
}
.body1-medium {
  font-size: 16px;
  font-weight: normal;
}
.body1-light {
  font-size: 16px;
  font-weight: lighter;
}

.body2-bold {
  font-size: 14px;
  font-weight: bold;
}
.body2-medium {
  font-size: 14px;
  font-weight: normal;
}
.body2-light {
  font-size: 14px;
  font-weight: lighter;
}

.caption {
  font-size: 12px;
  font-weight: lighter;
}
.button {
  font-size: 14px;
  font-weight: bold lighter;
}

